<template>
	<view>
		<camera device-position="front" flash="off" class="camera" @error="error" v-if="showCamera"></camera>
		<image mode="widthFix" class="image" :src="photoPath" v-if="showImage"></image>
		<view class="operate-container">
			<button type="primary" class="btn" @tap="clickBtn" :disabled="!canCheckin">{{ btnText }}</button>
			<button type="warn" class="btn" @tap="afresh" :disabled="!canCheckin">重拍</button>
		</view>
		<view class="notice-container">
			<text class="notice">注意事项</text>
			<text class="desc">拍照签到的时候，必须要拍摄自己的正面照片，侧面照片会导致无法识别。另外，拍照的时候不要戴墨镜或者帽子，避免影响拍照签到的准确度。</text>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			canCheckin: true,
			photoPath: '',
			btnText: '拍照',
			showCamera: true,
			showImage: false
		};
	},
	methods: {
		clickBtn: function() {
			let that = this;
			if (that.btnText == '拍照') {
				let ctx = uni.createCameraContext();
				ctx.takePhoto({
					quality: 'high', // 拍照质量
					success: function(resp) {
						console.log('获取拍照图片路径:' + resp.tempImagePath);

						// 赋值给模型层变量。
						that.photoPath = resp.tempImagePath;
						that.showCamera = false;
						that.showImage = true;
						that.btnText = '签到';
					}
				});
			} else {
				// 点击签到按钮要执行的代码
				uni.showLoading({
					title: '签到中请稍后'
				});

				//  签到要做
			}
		},

		// 重新拍照
		afresh: function() {
			let that = this;
			that.showCamera = true;
			that.showImage = false; // 图片隐藏起来图片
			// 页面的文字改为，拍照。
			that.btnText = '拍照';
		}
	},
	
	
};
</script>

<style lang="less">
@import url('checkin.less');
</style>
